.disc {
    position: relative;
    outline: none;
    background: #333;;
    width: 40px;
    height: 40px;
    border: 0;
    border-radius: 100px;
    margin: 0 0 10px 0;
    color: #FFF;
    cursor: pointer;
    text-align: center;
    font-size: 0.75em;
    line-height: 40px;
}

.disc.large {
    z-index: 1;
    position: relative;
    width: 60px;
    height: 60px;
    margin-bottom: 0px;
    border: 1px solid #fff;
}

.disc-label {
    position: absolute;
    top: 0;
    right: 2px;
    display: inline-block;
    width: 75px;
    height: 20px;
    line-height: 1em;
    background: #333;
    border-radius: 10px;
    padding: 4px;
    box-sizing: border-box;
    -webkit-transform: translate(-50%, 50%);
    transform: translate(-50%, 50%);
    color: #FFF;
    font-size: 10px;
    font-weight: 500;
    transition: 200ms ease;
}

.main-disc-icon {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    transition: background 0.3s ease;
}

.main-disc-icon, 
.main-disc-icon:before, 
.main-disc-icon:after {
    position: absolute;
    display: block;
    background: #FFF;
    width: 6px;
    height: 6px;
    border-radius: 10px;
}

.main-disc-icon:before, 
.main-disc-icon:after {
    content: '';
    transition: all 100ms ease, right 100ms 100ms ease, left 100ms 100ms ease;
}

.main-disc-icon:before {
    left: -11px;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.main-disc-icon:after {
    right: -11px;
    -webkit-transform: translateX(50%);
    transform: translateX(50%);
}

.main-disc-icon.active {
    background: transparent;
    transition: background 0.3s ease;
}

.main-disc-icon.active, 
.main-disc-icon.active:before, 
.main-disc-icon.active:after {
    height: 3px;
}

.main-disc-icon.active:before, 
.main-disc-icon.active:after {
    width: 20px;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    transition: all 100ms ease, width 100ms 100ms ease, -webkit-transform 300ms 200ms cubic-bezier(0.28, 0.55, 0.385, 1.65);
    transition: all 100ms ease, width 100ms 100ms ease, transform 300ms 200ms cubic-bezier(0.28, 0.55, 0.385, 1.65);
    transition: all 100ms ease, width 100ms 100ms ease, transform 300ms 200ms cubic-bezier(0.28, 0.55, 0.385, 1.65), -webkit-transform 300ms 200ms cubic-bezier(0.28, 0.55, 0.385, 1.65);
}

.main-disc-icon.active:before {
    left: 50%;
    -webkit-transform: translateX(-50%) rotate(45deg);
    transform: translateX(-50%) rotate(45deg);
}
.main-disc-icon.active:after {
    right: 50%;
    -webkit-transform: translateX(50%) rotate(-45deg);
    transform: translateX(50%) rotate(-45deg);
}

