  width: 2px;
  position: absolute;
  background: rgba(238, 238, 238, 0.3);
}
.line.l--0 {
  left: 38px;
}
.line.l--25 {
  left: 25%;
}
.line.l--50 {
  left: 50%;
}
.line.l--75 {
  left: 75%;
}
.line.l--100 {
  left: calc(100% - 1px);
}

.line__label {
  display: block;
  width: 100px;
  text-align: center;
  position: absolute;
  bottom: -20px;    
  right: -25px;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
