.animated-visibility-container {
    clear: both;
    text-align: left;
    position: relative;
}
.animated-visibility-container .is-hidden {
    visibility: hidden;
}
.animated-visibility-container .bounce-in {
    visibility: visible;
    -webkit-animation: bounce-in 0.4s;
    animation: bounce-in 0.4s;
}

@-webkit-keyframes bounce-in {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.5);
    }
  
    100% {
        -webkit-transform: scale(1);
    }
}
@keyframes bounce-in {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
  
    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

.timeline {
    max-width: 100%;
    padding: 15px;
    position: relative;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.timeline:before {
    content: "";
    position: absolute;
    top: 0px;
    left: 33%;
    bottom: 0px;
    width: 0px;
    border: 2px solid;
}
.timeline:after {
    content: "";
    display: table;
    clear: both;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .timeline{
        padding: 0;
    }

    .timeline:before {
        left: calc(1% + 6px);
        content: unset;
    }

    .timeline:after {
        content: unset;
    }
}
